import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import List from '@vkontakte/vkui/dist/components/List/List';
import FixedLayout from '@vkontakte/vkui/dist/components/FixedLayout/FixedLayout';
import Tabs from '@vkontakte/vkui/dist/components/Tabs/Tabs';
import TabsItem from '@vkontakte/vkui/dist/components/TabsItem/TabsItem';
import HorizontalScroll from '@vkontakte/vkui/dist/components/HorizontalScroll/HorizontalScroll';
import PanelHeaderBack from "@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack";
import Home from './Home';
import Favorites from './Favorites';
import styles from "./App.module.sass";

const Main = ({id, data, go, activeTab, tabHome, tabFavorites}) => {
    const renderHome = data.map(item => {
        return (
            <Home
                key={item.id}
                idItem={item.id}
                photo={item.photo}
                name={item.name}
                year={item.year}
                go={go}
            />
        );
    });

    const renderFavorites = data.map(item => {
        return (
            <Home
                key={item.id}
                id={item.id}
                photo={item.photo}
                name={item.name}
                year={item.year}
                go={go}
            />
        );
    });

    return (
        <Panel id={id}>
            <PanelHeader>Случайное кино</PanelHeader>
            <FixedLayout vertical="top">
                <Tabs theme="header" type="buttons">
                    <HorizontalScroll>
                        <TabsItem
                            onClick={tabHome}
                            selected={activeTab === 'home'}
                        >
                            Люди
                        </TabsItem>
                        <TabsItem
                            onClick={tabFavorites}
                            selected={activeTab === 'favorites'}
                        >
                            Сообщества
                        </TabsItem>
                    </HorizontalScroll>
                </Tabs>
            </FixedLayout>
            {activeTab === 'home'
                ?
                <Div>
                    <Div className={styles.button}>
                        <Button size="l" stretched style={{margin: 40}}>Найти</Button>
                        <Button size="l" stretched style={{margin: 40}}>Добавить</Button>
                    </Div>
                    <Div className={styles.list}>
                        {renderHome}
                    </Div>
                </Div>

                :
                ''
            }

            {activeTab === 'favorites'
                ?
                <Group title="default"
                       description="Чтобы найти фильм, нажмите кнопку - Найти,
					    чтобы добавить свой фильм, нажмите кнопку - Добавить.
					    ">
                    <List>
                        {renderFavorites}
                    </List>
                </Group>
                :
                ''
            }
        </Panel>

    );
};


export default Main;


